import { LinkButton } from '@components/ui/Buttons';
import { Heading1 } from '@components/ui/Headers';
import { json } from '@remix-run/node';
import { Outlet, useLoaderData } from '@remix-run/react';
import { getAllRoles } from './.server/role.queries';

export async function loader() {
  return json({
    roles: await getAllRoles(),
  });
}

export default function RolePermissions() {
  const { roles } = useLoaderData<typeof loader>();
  return (
    <>
      <Heading1>Permissions</Heading1>
      <div className="mt-5">
        {roles.length > 0 && (
          <table className="text-left table-auto w-full border-neutral-300 border-1">
            <thead>
              <tr className="border-b border-black">
                <th className="p-3">Role:</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {roles.map((role) => (
                <tr
                  key={role.uuid}
                  className="table-row border-b even:bg-zinc-50">
                  <td className="p-3">{role.name}</td>
                  <td className="text-right pr-5">
                    <LinkButton
                      variant="gray"
                      to={`/role/permissions/${role.uuid}`}>
                      Edit
                    </LinkButton>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
        {roles.length === 0 && <p>No roles found.</p>}
      </div>
      <Outlet />
    </>
  );
}
